import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { FormsModule }   from '@angular/forms';
import { FormHeaderComponent } from './form-header/form-header.component';
import { FormBodyComponent } from './form-body/form-body.component';
import { OrderformBodyComponent } from './orderform-body/orderform-body.component';
import { RouterModule, Routes } from '@angular/router';
import { SubmitComponent } from './submit/submit.component';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';

const routes: Routes = [
  {
    path: '',
    component: OrderformBodyComponent
  },
  {
    path: 'form',
    component: FormBodyComponent
  },
  {
    path: 'submit',
    component: SubmitComponent
  }
]

@NgModule({
  declarations: [
    AppComponent,
    FormHeaderComponent,
    FormBodyComponent,
    OrderformBodyComponent,
    SubmitComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(routes, {
      enableTracing: false,
      scrollPositionRestoration: 'top'
    })
  ],
  providers: [{provide: LocationStrategy, useClass: HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule { }
