import { Component, OnInit } from '@angular/core';
import { FormField } from '../FormField';
import { Router } from '@angular/router';

@Component({
  selector: 'app-form-body',
  template: `
    <form class="dynamic-form" #loanForm="ngForm" (ngSubmit)="submitForm();">
      <div *ngIf="formFields" class="row mt-5 ml-2 mr-2">
        <ng-template ngFor let-input [ngForOf]="formFields">
          <div class="col-12 mb-4">
            <label class="control-label text-bold font-weight-bold" [for]="input.id">{{input.name}}<span class="text-danger" *ngIf="input.required"> *</span></label>
            <input [value]="input.value" class="form-control no-gutters" type="text" id="input.id" [required]="input.required" [disabled]="input.disabled" 
              minlength="3" maxlength="25" (change)="updateInput(input, $event)" #field/>
            <div class="text-danger" *ngIf="input.error != null">{{input.error}}</div>
          </div>
        </ng-template>
      </div>

      <div class="text-center">
        <button class="btn btn-primary pl-5 pr-5 mt-5 mb-5" [disabled]="!loanForm.valid" (click)="submitForm()">Submit</button>
      </div>
    </form>
  `
})
export class FormBodyComponent implements OnInit {

  formFields: Array<FormField>;

  constructor(private router: Router) {}

  ngOnInit() {
    let inputs = this.router.url.split('?')[1];
    if (inputs == undefined || inputs.length == 0) {
      //test case
      this.formFields = [
        {
          id: "firstName",
          name: "First Name",
          value: "Laura",
          disabled: false,
          required: true
        },
        {
          id: "lastName",
          name: "Last Name",
          value: "Bailey",
          disabled: false,
          required: true
        },
        {
          id: "currentAddress",
          name: "Street Address",
          value: "8269 Trinket Ave.",
          disabled: false,
          required: true
        },
        {
          id: "currentCity",
          name: "City",
          value: "Los Angeles",
          disabled: false,
          required: true
        },
        {
          id: "currentState",
          name: "State",
          value: "California",
          disabled: false,
          required: true
        },
        {
          id: "currentZip",
          name: "Zip Code",
          value: "96513",
          disabled: false,
          required: true
        },
        {
          id: "ssn",
          name: "Social Security Number",
          value: "123-45-6789",
          required: false,
          disabled: false
        },
        {
          id: "creditScore",
          name: "Credit Score",
          value: "800",
          disabled: true,
          required: true
        }
      ];
    } else {
      this.formFields = [];
      this.constructForm(inputs);
    }
  }

  constructForm(inputs) {
    let params = inputs.split('&');
    for (let i = 0; i < params.length; i++) {
      let id = unescape(params[i].split('=')[0]);
      let val = unescape(params[i].split('=')[1]);
      let name;
      let required = true;
      let disabled = false;
      switch (id) {
        case "firstName":
          name = "First Name";
          break;
        case "lastName":
          name = "Last Name";
          break;
        case "currentAddress":
          name = "Street Address";
          break;
        case "currentCity":
          name = "City";
          break;
        case "currentState":
          name = "State";
          break;
        case "currentZip":
          name = "Zip Code";
          break;
        case "ssn":
          name = "Social Security Number";
          required = false;
          break;
        case "creditScore":
          name = "Credit Score";
          disabled = true;
          break;
        default:
          name = id;
          required = false;
          break;
      }

      this.formFields.push({
        id: id,
        name: name,
        value: val,
        required: required,
        disabled: disabled
      });
    }
  }

  submitForm() {
    let valid = true;
    for (let i = 0; i < this.formFields.length; i++) {
      if (!this.validateField(this.formFields[i])) {
        valid = false;
      }
    }
    if (valid) {
      let link = "https://kennethstsai.github.io/zillow/#/submit";
      window.open(link, "_self");
    }
  }

  updateInput(input, event) {
    let oldVal = input.value;
    input.value = event.target.value;
    if (!this.validateField(input)) {
      input.error = "This field is required.";
    } else {
      input.error = null;
    }
  }

  validateField(input) : boolean {
    let req = !input.required || input.value.length > 0;
    return req;
  }
}
